        p.mb-3 El prototipado se define como una técnica industrial en donde se crea un modelo tomando como referencia un producto para analizar su funcionalidad una vez este materializado. Se trata de un proceso planificado en el que se toman en cuenta distintos factores para determinar la utilidad y eficacia de un artículo. Esto se efectúa antes de que llegue a la fase de producción final. Es un recurso empleado por la gran mayoría de las empresas, especialmente, cuando deciden lanzar al mercado un nuevo servicio o artículo (Universidad Internacional de Valencia, 2023).

